body {
  width: 100%;
  height: 100%;
  margin: 0;
}

.container {
  background-color: #fff;
  border-radius: 30px;
  max-width: 90%;
  min-height: 70vh;
  margin: 50px auto;
  padding: 40px;
  box-shadow: 0 3px 8px #0000003d;
}

.input-area {
  position: relative;
}

input {
  border: 2px solid #cddaff;
  border-radius: 50px;
  outline: none;
  width: 85%;
  margin-bottom: 15px;
  padding: 10px;
  transition: padding-top .2s, margin-top .2s;
}

.input-area > #task-input:focus {
  border: none;
  border-bottom: 2px solid #cddaff;
  border-radius: 0;
  padding-top: 35px;
  transition: all .5s;
}

#task-input + label > span {
  color: #8f8f8f;
  margin: 0;
  font-size: 16px;
  font-weight: 300;
  transition: top .2s, font-size .2s, color .2s;
  position: absolute;
  top: 13px;
  left: 20px;
}

.task-input:focus + label > span {
  color: #333;
  top: 0;
}

.task-area {
  border-bottom: 1px solid #eee;
  outline: none;
}

.task-tabs {
  background-color: #f1f4f8;
  display: flex;
  position: relative;
}

.task-tabs div {
  cursor: pointer;
  padding: 5px 20px;
}

#underline {
  background-color: #cddaff;
  width: 50px;
  height: 4px;
  padding: 0;
  transition: all .5s;
  position: absolute;
  top: 30px;
  left: 0;
}

button {
  border-radius: 50px;
  width: 30px;
  height: 30px;
  font-size: 20px;
  transition: all .5s;
}

button:hover {
  background-color: #f1f4f89c;
}

.task {
  justify-content: space-between;
  padding: .5em 1em;
  display: flex;
  overflow: hidden;
}

.task > div {
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
  max-width: 70%;
  overflow: hidden;
}

.task-done {
  background-color: #f1f4f89c;
}

.task-done div:first-child {
  text-decoration: line-through 2px #8f8f8f;
  transition: all .5s .5s;
}

.checkBtn {
  background: url("check.0344f04b.png") center / 80% no-repeat;
  width: 30px;
  height: 30px;
  transition: all .5s;
}

.task-done .checkBtn {
  background: url("before.f8043cf2.png") center / 80% no-repeat;
  transition: all .5s;
}

.deleteBtn {
  background: url("delete.e6d6b0e5.png") center / 80% no-repeat;
  transition: all .5s;
}

button, select, textarea {
  color: inherit;
  vertical-align: middle;
  -webkit-appearance: none;
  border: none;
  font-family: Noto Sans KR, sans-serif;
}
